<template>
    <div class="auth_register">
        <div v-if="!isQz">
           <div class="title">快速注册</div>
           <el-form :model="formData" status-icon :rules="formRules" ref="form" label-position="top" v-if="user.domain.agent_id != 58150">
                <el-form-item label="用户名" prop="username">
                    <el-input v-model="formData.username" :disabled="!!localPhone" clearable placeholder="请输入账号，长度不能小于6位"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input type="password" clearable v-model="formData.password" autocomplete="off" placeholder="请输入密码，长度不能小于6位"></el-input>
                </el-form-item>
                <el-form-item label="确认密码" prop="rePassword">
                    <el-input type="password" clearable v-model="formData.rePassword" autocomplete="off" placeholder="请再次输入密码，长度不能小于6位"></el-input>
                </el-form-item>
                <el-form-item label="邀请码" prop="code" v-if="(!app.domain.invite_code && !localCode) && !localSign">
                    <el-input type="text" clearable v-model="formData.code" autocomplete="off" placeholder="请输入邀请码"></el-input>
                </el-form-item>
                <el-form-item class="submit_item">
                    <el-button type="primary" @click="submitForm" v-loading="loading">注册</el-button>
                </el-form-item>                             
                <div class="tips">
                    <span @click="pushPath('/auth/login')">登录账号</span>
                </div>
            </el-form>
            
            <div v-else style="padding: 28px;">
                请联系代理获取邀请码注册正式会员
            </div>
        </div>

        <div class="qz_container" v-else>
            <div class="qz_auth_login" :style="{ backgroundImage: 'url(' + loginBg + ')' }">
                <el-form :model="formData" status-icon :rules="formRules" ref="form" label-position="top">
                    <el-form-item label="用户名" prop="username">
                        <el-input v-model="formData.username" :disabled="!!localPhone" clearable placeholder="请输入账号，长度不能小于6位"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="password">
                        <el-input type="password" clearable v-model="formData.password" autocomplete="off" placeholder="请输入密码，长度不能小于6位"></el-input>
                    </el-form-item>
                    <el-form-item class="submit_item">
                        <el-button type="primary" class="login_btn" @click="submitFormQz" :loading="loading">账号注册/登录</el-button>
                        <!-- <div>
                            <router-link to="/android" target="_blank" v-if="app.isAndroid">APP下载</router-link>
                            <router-link to="/ios" v-if="app.isIOS">APP下载</router-link>
                        </div> -->
                    </el-form-item>
                    <!-- <div class="tips">
                        <span @click="pushPath('/auth/register')">注册账号</span>
                        <span @click="pushPath('/help')" style="float: right;">忘记密码？</span>
                    </div> -->
                </el-form>
            </div>
        </div>
    </div>

</template>

<script>
import { mapGetters } from 'vuex'
import request from '@/utils/request'
import { 
    passwordValidator,
    phoneCodeValidator,
    customCodeValidator,
    usernameValidator,
} from '@/utils/validator'
import { imgFormatter } from '@/utils'
import { isMobilePhone } from '@/utils'


export default {
    name: 'User-Register',

    data() {
        var validateRePassword = (rule, value, callback) => {
            if(value == '') {
                callback(new Error('不能为空'));
            }
            else if (value != this.formData.password) {
                callback(new Error('密码不一致'));
            } else {
                callback();
            }
        }

        return {
            dialogVisible: false,
            formData: {
                username: '',
                password: '',
                rePassword: '',
                code: '',
            },

            formRules: {
                username: { require: true, validator: usernameValidator, trigger: 'blur'},
                password: passwordValidator,
                rePassword: { require: true, validator: validateRePassword, trigger: 'blur'},
                code: customCodeValidator,
            },

            loading: false,

            loginBg: imgFormatter('/images/2019/09/29/edaf827d-2b47-4c9d-a571-526ba36c23b4.jpg')
        }
    },
    
    computed: {
		app() { return this.$store.getters.app },
		user() { return this.$store.getters.user },
        localCode() {
          return window.localStorage.getItem('code');
        },
        localSign() {
          return window.localStorage.getItem('sign');
        },
        localPhone() {
          return window.localStorage.getItem('phone');
        },
        queryPhone() {
          return this.$route.query.phone;
        },
		isQz() { return this.$route.name == 'qz-auth-register' },
    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            if(this.isQz) {
                if(this.queryPhone) {
                    this.formData.username = this.queryPhone;
                    window.localStorage.setItem('phone', this.queryPhone);
                }else if(this.localPhone) {
                    this.formData.username = this.localPhone;
                }
            }else if(this.localPhone) {
                this.formData.username = this.localPhone;
            }

            if(this.$route.query.sign) {
                window.localStorage.setItem('sign', this.$route.query.sign);
            }
        },

        submitForm() {
            this.formData.username = this.formData.username.trim();

            const formData = Object.assign({}, this.formData);

            if(this.app.domain.invite_code) {
                formData.code = this.app.domain.invite_code;
            }

            if(this.localCode) {
                formData.code = this.localCode;
            }
            
            if(this.localSign) {
                formData.code = this.localSign;
                formData.sign = this.localSign;
            }


            this.$refs.form.validate((valid) => {
                if (valid) {
                    var data = {
                        username: formData.username,
                        password: formData.password,
                        code: formData.code,
                        // phone: window.localStorage.getItem('phone'),
                    };

                    if(formData.sign) {
                        data.sign = formData.sign;
                    }

                    if(data.phone && !isMobilePhone(data.phone)) {
                        this.$message.error('手机号格式错误');
                        return;
                    }

                    if(formData.rePassword != formData.password) {
                        this.$message.error('两次密码不一致');
                        return;
                    }

                    this.$store.dispatch('userRegister', data).then((json) => {
                        if(json.code == 200) {

                            if(this.localPhone) {
                                window.localStorage.removeItem('phone');
                            }

                            if(this.app.isApp) {
                                this.$message({
                                    message: '注册成功',
                                    type: 'success',
                                    duration: 1000,
                                    onClose:() => {
                                        this.$store.dispatch('postMsgToApp', {
                                            action: 'register',
                                            data: {
                                                token: json.token
                                            },
                                        })
                                    }
                                });
                            }
                            // this.$message({
                            //     message: '注册成功',
                            //     type: 'success',
                            // });
                            
                            // this.$router.push({ path: '/' })
                        }
                    })
                } else {
                    console.log('提交错误');
                    return false;
                }
            });
        },

        submitFormQz() {
            this.formData.username = this.formData.username.trim();

            this.$refs.form.validate((valid) => {
                if (valid) {
                    if(this.loading) return ;

                    var data = {
                        username: this.formData.username,
                        password: this.formData.password,
                        // phone: window.localStorage.getItem('phone'),
                        sign: this.localSign,
                    };

                    if(!data.sign) {
                        this.$message.error('缺少Token');
                        return ;
                    }else {
                        data.code = data.sign;
                    }

                    if(data.phone && !isMobilePhone(data.phone)) {
                        this.$message.error('手机号格式错误');
                        return;
                    }

                    // let winOpen = window.open("", "product");
                    
                    data._stopReload = true;

                    this.loading = true;
                    this.$store.dispatch('loginAndRegisterByUsername', data).then((json) => {
                        this.loading = false;

                        if(json.code == 200) {

                            if(this.localPhone) {
                                window.localStorage.removeItem('phone');
                            }

                            const link = this.$router.resolve('/nav/3');
                            window.location = link.href;
                            // window.location.reload();
                            // const link = this.$router.resolve('/redirect?pid=' + 23);
                            // winOpen.location = link.href;
                            // window.location.reload();
                        }else {
                            // winOpen.close();
                        }
                    });
                } else {
                    console.log('提交错误');
                    return false;
                }
            });
        },

        pushPath(path) {
            // if(this.app.isApp) {
            //     this.$store.dispatch('postMsgToApp', {
            //         action: 'presentModal',
            //         data: {
            //             url: path,
            //         }
            //     })
            //     return ;
            // }

            this.$router.push({ path: path });
        }
    }
}
</script>

